import React, {PureComponent} from 'react'
import {
  message,
  Button,
  Tabs,
  Modal
} from 'antd'
import style from './style/Tool.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {setVariant} from 'Variant'
import {QYG, SCTYPE} from 'Mock'
import {getNowPageId} from '../../global/components/menuData'

const {TabPane} = Tabs
const confirm = Modal.confirm
@connect(
  state => ({...state.outWarehouseListReplenishment}),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  componentDidMount() {
    const {GetButtonPower} = this.props
    const MEUN = JSON.parse(localStorage.getItem('b2bUserMenu'))
    const ID = getNowPageId(MEUN, window.location.pathname)
    GetButtonPower({urlParams: [ID]})
  }
  // 
  handleOut = () => {
    const {handleGetDetailDelivery, selectedRowKeys} = this.props
    let data = []
    if (!selectedRowKeys.length) {
      message.error('')
      return
    }
    selectedRowKeys.map((o) => {
      data.push({
        id: o
      })
    })
    handleGetDetailDelivery({data}, ({result}) => {
      const {handleGetList, searchParams, size, page, handleExport} = this.props
      confirm({
        title: '',
        content: result.message,
        okText: result.hasError ? '' : '',
        width: 520,
        cancelText: '',
        onCancel() {
          handleGetList({searchParams: {page, size, ...searchParams}})
        },
        onOk() {
          // 
          if (!result.hasError) {
            handleGetList({searchParams: {page, size, ...searchParams}})
            return
          }
          handleExport({
            searchParams: {shortageList: result.shortageList || ''}
          }, () => {
            message.success('')
            handleGetList({searchParams: {page, size, ...searchParams}})
          })
        }
      })
    })
  }

  /*  */
  handleBrowse = () => {
    let {selectedRows} = this.props
    if (!selectedRows.length) {
      message.error('')
      return
    }
    // selectedRows = selectedRows.filter((o) => o.status == 6 || o.status == 7 || o.status == 8)
    // if (!selectedRows.length) {
    //   message.error('')
    //   return
    // }

    let type = 1 // wms
    let pass = true
    let nowType = ''
    //
    for (let i of Object.keys(selectedRows)) {
      if (i == 0) {
        nowType = selectedRows[i].businessType
      } else {
        if (nowType != selectedRows[i].businessType) {
          pass = false
          break
        }
      }
    }
    if (!pass) {
      message.error('')
      return
    }
    // 0
    new Promise((resolve, reject) => {
      // if (SCTYPE.indexOf(nowType) != -1) {
      //   confirm({
      //     title: '',
      //     content: '0',
      //     cancelText: '',
      //     okText: '',
      //     onOk() {
      //       resolve(true)
      //     },
      //     onCancel() {
      //       resolve(false)
      //     }
      //   })
      // } else {
      //   resolve(false)
      // }
      resolve(false)
    }).then(r => {
      setVariant('wms-print-filterZero', r)
      //  1: wms , 2 
      QYG.indexOf(nowType) != -1 ? type = 2 : type = 1
      if (type == 1) {
        let ids = []
        selectedRows.map((o) => {
          ids.push(o.id)
        })
        setVariant('wms/outWarehouseList', ids)
        window.open('/wms/outWarehouseList/Print', 'newWindow', 'height=500, width=1300, top=0, left=0, toolbar=no, menubar=no, scrollbars=no, resizable=no, location=no, status=no')
      } else {
        let orderList = [],
          reqData = []
        selectedRows.map((o) => {
          orderList.push(o.orderCode)
          reqData.push({
            id: o.id
          })
        })
        setVariant('orderList', orderList)
        const {handleUpdatePrintNum} = this.props
        handleUpdatePrintNum({data: reqData})
        window.open('/sms/outingListBatch', 'newWindow', 'height=500, width=1300, top=0, left=0, toolbar=no, menubar=no, scrollbars=no, resizable=no, location=no, status=no')
      }
    })
  }
    // 
  handleExport = () => {
    const {handleExportList, searchParams, page, size, tableActiveKey} = this.props
    handleExportList({searchParams: {page, size, orderType: 'B', status: tableActiveKey, ...searchParams, productName: searchParams.productName ? searchParams.productName.children : ''}}, () => {
      message.success('')
    })
  }
  // 
  query = (tableActiveKey) => {
    const {handleGetList, searchParams, size, update} = this.props
    let reqObj = {...searchParams}
    update({tableActiveKey})
    handleGetList({searchParams: {...reqObj, page: 1, status: tableActiveKey, size}}, true)
  }
  render() {
    const {tableActiveKey, tab0, tab6, tab8, tab9, modalTuttonLoading, outWarehouseListReplenishmentOne} = this.props
    return (
      <div className={style['table-tool']} style={{paddingBottom: 0}}>
        {
          outWarehouseListReplenishmentOne && <Button type="primary" className={style.btn} onClick={this.handleOut} loading={modalTuttonLoading} disabled={tableActiveKey != '0'}></Button>
        }
        <Button type="primary" className={style.btn} onClick={this.handleExport}></Button>
        <Button type="primary" className={style.btn} onClick={this.handleBrowse}></Button>
        <Tabs onChange={this.query} activeKey={tableActiveKey} >
          <TabPane tab={`(${tab0 || 0})`} key="0"></TabPane>
          <TabPane tab={`(${tab6 || 0})`} key="6"></TabPane>
          <TabPane tab={`(${tab8 || 0})`} key="8"></TabPane>
          <TabPane tab={`(${tab9 || 0})`} key="9"></TabPane>
        </Tabs>
      </div>
    )
  }
}



