import * as actionType from './actionType'
import {getData} from 'util'
import {API_SHIPPEDORDERS_PAGE, API_SHIPPEDORDERS_SHIPPEDTRACES, API_SHIPPEDORDERS_REPLENISHMENTORDERSHIPPED, API_SHIPPED_LOADCOUNTS, API_SHIPPEDORDERS_EXPORTREPLENISHMENT, API_SHIPPEDORDERS_EXPORT, API_SHIPPEDORDERS_PRINTTIMES, API_GET_MENUS_PERMISSIONS_BANS} from 'api'
import moment from 'moment'

//
export const update = (object) => ({
  type: actionType.OUTWAREHOUSELISTREPLENISHMENT_UPDATE,
  data: object
})

//

// 
export const handleGetList = (reqData, status) => (dispatch) => {
  dispatch({type: actionType.OUTWAREHOUSELISTREPLENISHMENT_UPDATE, data: {tableLoading: true}})
  let reqObjSearchParams = {...reqData.searchParams, orderType: 'B'}
  for (let i in reqObjSearchParams) {
    if (Object.prototype.toString.call(reqObjSearchParams[i]) === '[object Object]') {
      reqObjSearchParams[i] = reqObjSearchParams[i].value
    }
  }
  if (reqObjSearchParams.initDate && reqObjSearchParams.initDate.length) {
    reqObjSearchParams.createTimeStart = moment(reqObjSearchParams.initDate[0]).format('YYYY-MM-DD')
    reqObjSearchParams.createTimeEnd = moment(reqObjSearchParams.initDate[1]).format('YYYY-MM-DD')
  }
  if (reqObjSearchParams.initDate2 && reqObjSearchParams.initDate2.length) {
    reqObjSearchParams.sendDateStart = moment(reqObjSearchParams.initDate2[0]).format('YYYY-MM-DD')
    reqObjSearchParams.sendDateEnd = moment(reqObjSearchParams.initDate2[1]).format('YYYY-MM-DD')
  }
  delete reqObjSearchParams.initDate2
  delete reqObjSearchParams.initDate
  reqData.searchParams = reqObjSearchParams

  if (status) {
    getData({api: API_SHIPPEDORDERS_PAGE, ...reqData}).then(data => {
      // 
      let {page, size} = reqData.searchParams
      dispatch({
        type: actionType.OUTWAREHOUSELISTREPLENISHMENT_UPDATE,
        data: {
          page,
          size,
          tableLoading: false,
          total: data.result.total,
          selectedRowKeys: [], // key
          selectedRows: [], // 
          searchResult: data.result.list || []
        }
      })
    }).catch(err => {
      dispatch({
        type: actionType.OUTWAREHOUSELISTREPLENISHMENT_FILE
      })
    })
  } else {
    // tabs
    getData({api: API_SHIPPED_LOADCOUNTS, ...reqData}).then(({result}) => {
      // 
      let arr = ['0', '6', '8', '9']
      let status = ''
      let tabNumObj = {
        tab0: '',
        tab6: '',
        tab8: '',
        tab9: ''
      }
      result.map((o) => {
        tabNumObj[`tab${o.key}`] = o.value
      })
      for (let i = 0; i < arr.length; i++) {
        for (let j = 0; j < result.length; j++) {
          if (result[j].key == arr[i] && result[j].value) {
            status = arr[i]
            break
          }
        }
        if (status) break
      }
      if (status) {
        reqData.searchParams.status = status
      }
      getData({api: API_SHIPPEDORDERS_PAGE, ...reqData}).then(data => {
        // 
        let {page, size} = reqData.searchParams
        dispatch({
          type: actionType.OUTWAREHOUSELISTREPLENISHMENT_UPDATE,
          data: {
            page,
            size,
            tableLoading: false,
            total: data.result.total,
            tableActiveKey: status,
            selectedRowKeys: [], // key
            selectedRows: [], // 
            searchResult: data.result.list || [],
            ...tabNumObj
          }
        })
      }).catch(err => {
        dispatch({
          type: actionType.OUTWAREHOUSELISTREPLENISHMENT_FILE
        })
      })
    }).catch(err => {
      dispatch({
        type: actionType.OUTWAREHOUSELISTREPLENISHMENT_FILE
      })
    })
  }
}

// 
export const handleGetDetailRecord = (reqData) => (dispatch) => {
  dispatch({type: actionType.OUTWAREHOUSELISTREPLENISHMENT_UPDATE, data: {deliveryTableLoading: true}})
  getData({api: API_SHIPPEDORDERS_SHIPPEDTRACES, ...reqData}).then(data => {
    // 
    dispatch({
      type: actionType.OUTWAREHOUSELISTREPLENISHMENT_UPDATE,
      data: {
        deliveryTableLoading: false,
        searchResultRecord: data.result || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.OUTWAREHOUSELISTREPLENISHMENT_FILE
    })
  })
}

// 
export const handleGetDetailDelivery = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.OUTWAREHOUSELISTREPLENISHMENT_UPDATE, data: {modalTuttonLoading: true}})
  getData({api: API_SHIPPEDORDERS_REPLENISHMENTORDERSHIPPED, ...reqData}).then(data => {
    // 
    dispatch({
      type: actionType.OUTWAREHOUSELISTREPLENISHMENT_UPDATE,
      data: {
        modalTuttonLoading: false,
        modalData: data.result || {}
      }
    })
    callBack && callBack(data)
  }).catch(err => {
    dispatch({
      type: actionType.OUTWAREHOUSELISTREPLENISHMENT_FILE
    })
  })
}

