export const GetButtonPower = (reqData) => (dispatch) => {
  getData({api: API_GET_MENUS_PERMISSIONS_BANS, ...reqData}).then(({data}) => {
    console.log(data)
    // 
    let Power = {}
    data.map((o) => {
      Power[o.menuCode] = true
    })
    dispatch({
      type: actionType.OUTWAREHOUSELISTREPLENISHMENT_UPDATE,
      data: {
        ...Power
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.OUTWAREHOUSELISTREPLENISHMENT_FILE
    })
  })
}



