import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  DatePicker,
  message,
  Input
} from 'antd'
import style from './style/Form.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {API_SHIPPEDORDERS_BUSINESSKEYVALUES, API_SHOPINFOBYSHOPCODE_KEYVALUES, API_PRODUCT_GETPRODUCTKEYVALUES} from 'api'
import {CsxSelect, CsxMultipleSearch, CsxSearch} from 'Form'
import moment from 'moment'
const {RangePicker} = DatePicker

@connect(
  state => ({...state.outWarehouseListReplenishment, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  componentWillMount() {
    this.initDateOpt()
  }
  //    - 
  initDateOpt(values) {
    const {update} = this.props
    const preMonth = moment().subtract(1, 'M').startOf('day')
    update({searchParams: {initDate: [preMonth, moment().endOf('day')]}})
  }
  // 
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }
  // 
  query = () => {
    const {handleGetList, searchParams, size} = this.props
    let reqObj = {...searchParams}

    const {initDate = []} = searchParams
    // 
    if (!initDate || (Array.isArray(initDate) && initDate.length < 2)) {
      message.error('')
    }
    handleGetList({searchParams: {...reqObj, page: 1, size}})
  }
  // 
  reset = () => {
    const {update} = this.props
    update({searchParams: {}})
     // 
    this.initDateOpt()
  }
  render() {
    const {
      searchParams: {
        initDate,
        orderCode,
        businessType,
        customerName,
        shopCode,
        productCode,
        sourceSystem,
        initDate2
      },
      userLoation
    } = this.props
    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        // gutter={16}
        >
          <Col span={6}>
            <div className='commonInput'>
              <label></label>
              <div className='box'>
                <Input placeholder='' value={orderCode} onChange={(e) => {
                  this.onInputChange({orderCode: e.target.value})
                }} />
              </div>
            </div>
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_SHIPPEDORDERS_BUSINESSKEYVALUES}
              label=''
              searchParams={{orderType: 'S'}}
              selectItem={{data: businessType, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({businessType: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxMultipleSearch
              dateApi={API_SHOPINFOBYSHOPCODE_KEYVALUES}
              label=''
              searchParamsKey='shopCode'
              selectItem={{data: shopCode, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({shopCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <div className='commonInput'>
              <label></label>
              <div className='box'>
                <Input value={customerName} onChange={(e) => {
                  this.onInputChange({customerName: e.target.value})
                }}/>
              </div>
            </div>
          </Col>
          <Col span={6}>
            <CsxSearch
              dateApi={API_PRODUCT_GETPRODUCTKEYVALUES}
              label=''
              searchParamsKey='productCode'
              searchParams={{warehouseCode: userLoation.value}}
              selectItem={{data: productCode, key: 'key', valueKey: ['key', 'value']}}
              onSelect={(value) => {
                this.onInputChange({productCode: value})
              }}
              onChangeSelect={(value) => {
                this.onInputChange({productCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSelect
              data={[{key: 'CSMS', value: ''}, {key: 'SSMS', value: ''}]}
              label=''
              selectItem={{data: sourceSystem, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({sourceSystem: value})
              }}
            />
          </Col>
          <Col span={8}>
            <div className='commonInput'>
              <label></label>
              <div className='box' style={{marginRight: 0}}>
                <RangePicker onChange={(moment, string) => {
                  this.onInputChange({initDate: moment})
                }}
                  format={'YYYY-MM-DD'}
                  value={initDate}
                  allowClear={false}
                />
              </div>
            </div>
          </Col>
          <Col span={8}>
            <div className='commonInput'>
              <label></label>
              <div className='box' style={{marginRight: 0}}>
                <RangePicker onChange={(moment, string) => {
                  this.onInputChange({initDate2: moment})
                }}
                  format={'YYYY-MM-DD'}
                  value={initDate2}
                />
              </div>
            </div>
          </Col>
          <Col span={16}>
            <Row type="flex" justify="end" >
              <Button type="primary" className={style.mr10} onClick={this.query} ></Button>
              <Button type="primary" className={style.mr10} onClick={this.reset} ></Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



