import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Table,
  Button,
  message
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
const confirm = Modal.confirm
import {CsxValidateInput} from 'Form'
const THROUGH = 'T02'

@connect(
  state => ({...state.outWarehouseListReplenishment}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {

    }
    this.columns = [{
      title: '',
      width: 150,
      dataIndex: 'productCode',
      key: 'productCode'
    }, {
      title: '',
      width: 150,
      dataIndex: 'productName',
      key: 'productName'
    }, {
      title: '',
      width: 100,
      dataIndex: 'unit',
      key: 'unit'
    }, {
      title: '',
      width: 100,
      dataIndex: 'planQty',
      key: 'planQty'
    }, {
      title: '',
      width: 150,
      dataIndex: 'pickingSideQty',
      key: 'pickingSideQty',
      render: (text, record, index) => record.businessType == THROUGH ? null : <section>
        <div>(: {record.pickingSideAvailableQty})</div>
        <CsxValidateInput style={{width: '100px'}} reg={(record.unit && record.unit.toUpperCase()) || 'default'} error={record.pickingSideQtyInput ? (record.pickingSideQtyInput.error && (record.storeLocationQtyInput ? record.storeLocationQtyInput.error : true)) : record.storeLocationQtyInput ? record.storeLocationQtyInput.error : true} handleOnchange={(data) => {
          record.pickingSideQtyInput = data
          this.forceUpdate()
        }} />
      </section>
    }, {
      title: '',
      width: 150,
      dataIndex: 'storeLocationQty',
      key: 'storeLocationQty',
      render: (text, record, index) => record.businessType == THROUGH ? null : <section>
        <div>(: {record.storeLocationAvailableQty})</div>
        <CsxValidateInput style={{width: '100px'}} reg={(record.unit && record.unit.toUpperCase()) || 'default'} error={record.storeLocationQtyInput ? (record.storeLocationQtyInput.error && (record.pickingSideQtyInput ? record.pickingSideQtyInput.error : true)) : record.pickingSideQtyInput ? record.pickingSideQtyInput.error : true} handleOnchange={(data) => {
          record.storeLocationQtyInput = data
          this.forceUpdate()
        }} />
      </section>
    }, {
      title: '',
      width: 150,
      dataIndex: 'shippedQtyInput',
      key: 'shippedQtyInput',
      render: (text, record, index) => record.businessType == THROUGH ? <section>
        <div>(: {record.ykStoreLocationAvailableQty})</div>
        <CsxValidateInput style={{width: '100px'}} reg={(record.unit && record.unit.toUpperCase()) || 'default'} error={record.shippedQtyInput ? record.shippedQtyInput.error : true} handleOnchange={(data) => {
          record.shippedQtyInput = data
          this.forceUpdate()
        }} />
      </section> : null
    }, {
      title: '',
      width: 150,
      dataIndex: 'shortageQty',
      key: 'shortageQty'
    }, {
      title: '',
      width: 150,
      dataIndex: 'shippedQty',
      key: 'shippedQty'
    }]
  }
  componentDidMount() {
    this._getAsyncData()
  }
  // tabskey
  _getAsyncData() {
    const {handleGetDetailDelivery, modalId} = this.props
    handleGetDetailDelivery({urlParams: modalId})
  }
  // 
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowDeliveryModal: false})
  }

  // 
  delivery = () => {
    const {handleStock, modalData} = this.props
    let reqData = {
      orderCode: modalData.orderCode,
      requestList: []
    }
    let error = false
    modalData.shippedOrderItemList.map((o, i) => {
      let obj = {
        id: o.id,
        orderCode: o.orderCode,
        planQty: o.planQty,
        productCode: o.productCode,
        shippedQty: o.shippedQty,
        shortageQty: o.shortageQty
      }
      if (o.businessType == THROUGH) {
        if (o.shippedQtyInput ? o.shippedQtyInput.error : true) {
          error = true
          return
        }
        obj.shippedQty = o.shippedQtyInput.value
      } else {
        if ((o.pickingSideQtyInput ? o.pickingSideQtyInput.error : true) && (o.storeLocationQtyInput ? o.storeLocationQtyInput.error : true)) {
          error = true
          return
        }
        obj.pickingSideQty = o.pickingSideQtyInput && o.pickingSideQtyInput.value
        obj.storeLocationQty = o.storeLocationQtyInput && o.storeLocationQtyInput.value
      }
      reqData.requestList.push(obj)
    })
    if (error) {
      message.error('')
      return
    }
    let _this = this
    confirm({
      title: '',
      content: '',
      okText: '',
      width: 520,
      cancelText: '',
      onOk() {
        handleStock({data: reqData}, () => {
          message.success('')
          const {handleGetList, searchParams, page, size} = _this.props
          handleGetList({searchParams: {page, size, ...searchParams}})
          _this.handleCloseModal()
        })
      }
    })
  }
  render() {
    const {deliveryTableLoading, modalData} = this.props
    return (
      <Modal
        title=""
        visible={true}
        onCancel={this.handleCloseModal}
        footer={null}
        mask={false}
        width={1000}
      >
        <div className='content'>
          <div className='header'>
            <Row type="flex" justify="start">
              <Col span={8} className='delBox'>
                <label></label>
                <span>{modalData.orderCode || ''}</span>
              </Col>
              <Col span={8} className='delBox'>
                <label></label>
                <span>{modalData.businessTypeDesc || ''}</span>
              </Col>
              <Col span={8} className='delBox'>
                <label></label>
                <span>{modalData.customerName || ''}</span>
              </Col>
              <Col span={8} className='delBox'>
                <label></label>
                <span>{modalData.receiveName || ''}</span>
              </Col>
              <Col span={8} className='delBox'>
                <label></label>
                <span>{`${modalData.shopCode || ''}-${modalData.shopName || ''}`}</span>
              </Col>
              <Col span={8} className='delBox'>
                <label></label>
                <span>{`${modalData.supplierCode || ''}-${modalData.supplierName || ''}`}</span>
              </Col>
            </Row>
          </div>
          <div className='foot'>
            <div className='table-group' style={{margin: 0}}>
              <Table
                rowKey={(record, index) => index}
                loading={deliveryTableLoading}
                columns={this.columns}
                scroll={{x: 1400, y: 400}}
                dataSource={modalData.shippedOrderItemList}
                pagination={false}
              />
              <Row type="flex" justify="end" style={{marginTop: '15px'}}>
                <Col>
                  <Button type="primary" onClick={this.delivery} ></Button>
                </Col>
              </Row>
            </div>
          </div>
        </div>
      </Modal>
    )
  }
}



